.App {
  text-align: center;
  background-image: url('./media//SL_072620_32930_21.jpg');
  font-family: 'Chewy', cursive;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.buttons {
  display: flex;
  flex-direction: 'row';
  justify-content: space-evenly;
  background: white;
  margin: 30px 150px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgb(0 0 0 / 20%);
  color: #4a9d02;
  padding: 10px 0;
}

.animal-button {
  background-size: cover;
  height: 100px;
  width: 100px;
  cursor: pointer;
  background-color: white;
  border: none;
  margin: 20px;
}

.animal-button:hover {
  transform: translateY(5px);
}

.cow {
  background-image: url('./media/cow.svg');
}

.pig {
  background-image: url('./media/pig.svg');
}

.chicken {
  background-image: url('./media/chicken.svg');
}

.sheep {
  background-image: url('./media/sheep.svg');
}

.all-button {
  padding: 10px;
  margin: 10px;
  height: 50px;
  width: 80px;
  align-self: center;
  cursor: pointer;
  border-radius: 10px;
  color: white;
  background-color: crimson;
  border: none;
  font-family: 'Chewy', cursive;
  font-size: 25px;
  box-shadow: 0px 0px 10px 0px rgb(0 0 0 / 20%);
}

.all-button:hover {
  transform: translateY(5px);
}

.plots {
  display: flex;
  flex-direction: column;
  padding: 30px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px rgb(0 0 0 / 20%);
  margin: 20px 150px
}

.plot {
  width: 1200px;
  height: 800px;
  align-self: center;
}

.big-text {
  color: snow;
  text-shadow: 3px 0px 5px rgba(0,0,0,0.42), 0px 3px 5px rgba(0,0,0,0.42);
}

.page-title {
  font-size: 110px;
  padding: 20px;
  line-height: 200px;
}

.selected-animal {
  font-size: 30px;
}

.animal-group {
  display: flex;
  flex-direction: column;
  font-size: 30px;
  border-radius: 100%;
}

.selected {
  color: crimson;
}
